.mainform{
    background-color: #947bf1;
    padding: 1rem;
    margin: 2rem auto; 
    width: 30rem; 
    max-width: 95%;
    border-radius: 12px;
    text-align: center;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
}

.mainform button {
    font: inherit;
    cursor: pointer;
    padding: 1rem 2rem;
    border: 1px solid #40005d;
    background-color: #40005d;
    color: white;
    border-radius: 12px;
     margin-right: 1rem; 
  }