@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  background-color: #628ca5ea;
 

}


.Card{
    border-radius: 12px;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);  
}
.items{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  
  padding: 0.5rem;
  margin: 1rem 0;
 
  background-color:#4b4b4b;
}
.alining{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  
  
}
.expense__price{
    font-size: 1.3rem;
  
    color: rgb(145, 207, 236);
   
  
}
.button{
  border:1px solid white;
  background-color: black;
  color:white;
  font-weight: bold;
  padding:0.8rem;
  border-radius:12px;
  cursor: pointer;
}
.expense__title{
    font-size: 1.4rem;
 
  font-weight: bold;
  color: white;
}

.date{
border-radius: 12px;
margin-right:1.5rem;
background-color:#40005d;
border:1px solid white;
padding:0.5rem;
 


}
.expense__month{
  font-size: 1rem;
   
  color: white;
  display: block;
  text-align: center;
}

.expense__day{
  text-align: center;
  font-size: 1.5rem;
  font-weight: bold;
  padding:0.2rem;
  color: white;
  display: block;
}
.expenses{
    background-color: black;
    margin:2rem auto;
    width:50rem;
    max-width: 95%; 
   padding:1rem;
}
.nodetails{
    color: white;
    font-weight: bold;
    text-align: center;
}
.monthly__filter{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    color: white;
    font-weight: bold;
    padding: 1rem;
}
select{
    border:1px solid white;
    background-color: black;
    color:white;
    font-weight: bold;
    padding:0.8rem;
    font-size: 1rem;
    border-radius:12px;

}
