    select{
        font-weight:normal;
    font-size: 0.5rem;
    padding:0.4rem;
    }
    .monthly__filter{
        padding:0.5rem;
    }

}  */
.total{

    background-color: #7e5ef1;
    padding: 0.5rem;
    margin: 1rem; 
    width: 50rem; 
    max-width: 95%;
    border-radius: 12px;
    display: inline-block;
    color: white;
    text-align: center;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
}

@media(max-width:39rem){
    .total{
        margin: 1rem 0.5rem; 
    }
}
.total_form{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
   -webkit-justify-content: center;
           justify-content: center;
   -webkit-align-items: center;
           align-items: center;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 1rem;

    text-align: left;
}
.control_form label {
   
    font-weight: bold;
    margin-bottom: 0.5rem; 
   display: block; 
  }
  
  .control_form input {
   
    font: inherit;
    padding: 0.5rem;
    border-radius: 6px;
    border: 1px solid #ccc;
    width: 20rem;
    max-width: 100%;
  }
.mainform{
    background-color: #947bf1;
    padding: 1rem;
    margin: 2rem auto; 
    width: 30rem; 
    max-width: 95%;
    border-radius: 12px;
    text-align: center;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
}

.mainform button {
    font: inherit;
    cursor: pointer;
    padding: 1rem 2rem;
    border: 1px solid #40005d;
    background-color: #40005d;
    color: white;
    border-radius: 12px;
     margin-right: 1rem; 
  }
